import React from "react"
import { Button } from "reactstrap"
import DataTable from "react-data-table-component"
import { Edit, Trash2 } from "react-feather";
import Can from '../../../../../utility/permissions/can';
class WeightsInspectionTable extends React.Component {

    tableProperties = {
        columns: [
            {
                name: "Action",
                cell: (row) => (
                    <>
                        <Can I="update" a={this.props.role} passThrough>
                            {allowed =>
                                <Button.Ripple className="mr-1 mb-1 bg-gradient-success" size="sm" color="none"
                                    disabled={!allowed}
                                    onClick={() => this.props.handleEdit(row)}
                                >
                                    <Edit size={14} />
                                </Button.Ripple>
                            }
                        </Can>

                        <Can I="delete" a={this.props.role} passThrough>
                            {allowed =>
                                <Button.Ripple className="mr-1 mb-1  bg-gradient-danger" size="sm" color="none"
                                    disabled={!allowed}
                                    onClick={() => this.props.handleDelete(row)}
                                ><Trash2 size={14} />
                                </Button.Ripple>
                            }
                        </Can>
                    </>
                )
            },
            {
                name: "Busienss Name",
                selector: "BUSINESS_NAME",
                sortable: true,
                cell: row => <p className="mb-0">{row.BUSINESSES.BUSINESS_NAME}</p>
            },
            {
                name: "Client Name",
                selector: "CLIENT_NAME",
                sortable: true,
                cell: row => <p className="mb-0">{row.BUSINESSES.CLIENTID.CLIENT_NAME}</p>
            },
            {
                name: "Registration Number",
                selector: "REG_WEIGHT_NUMBER",
                sortable: true,
                cell: row => <p className="mb-0">{row.REG_WEIGHT_NUMBER}</p>
            },
            {
                name: "Inspection Status",
                selector: "SYS_STATUS_NAME",
                sortable: true,
                cell: row => <p className="mb-0">{row.SYS_STATUS_NAME}</p>
            },



        ]
    }

    render() {

        return (
            <>
                <DataTable
                    className="dataTable-custom"
                    data={this.props.value.length ? this.props.filteredData : this.props.data}
                    columns={this.tableProperties.columns}
                    noHeader
                    pagination
                    subHeader
                    striped
                    responsive

                />
            </>

        )
    }
}


export default WeightsInspectionTable