import React from 'react';
import {
    Button, Modal, Form, InputGroup, Col
} from 'react-bootstrap';
import { Search,Eye } from 'react-feather';

class WeightsInspectionModal extends React.Component {
    render() {
        return (
            <div>
                <Modal
                    show={this.props.show}
                    onHide={this.props.handleHide}
                    size="lg"
                >
                    <Modal.Header closeButton>
                        <Modal.Title id="AddUpdate">
                            {this.props.InsertUpdate} Weight Inspection
                        </Modal.Title>
                    </Modal.Header>

                    <Form noValidate validated={this.props.validated} onSubmit={this.props.handleSubmit} autoComplete="off">
                        <Modal.Body>
                            <Form.Row>
                            <Form.Group as={Col} md="6" controlId="BusinessName">
                            
                            <Form.Label>Business Name</Form.Label>

                            <InputGroup>
                            <InputGroup.Prepend>
                                    <Button variant="info" size="sm" onClick={this.props.handleShowBusinessSearchEye}>
                                        <Eye />
                                    </Button>
                                </InputGroup.Prepend>
                                <Form.Control
                                    type="text"
                                    name="BusinessName"
                                    value={this.props.BusinessName}
                                    onChange={this.props.changeBusinessName}
                                    placeholder="Search Business"
                                    aria-describedby="inputGroupPrepend"
                                    
                                    required
                                />
                               
                                
                            </InputGroup>

                        </Form.Group>
                        <Form.Group as={Col} md="6" controlId="ClientID">
                                    <Form.Label>Client Name</Form.Label>
                                    <InputGroup>
                                    <InputGroup.Prepend>
                                            <Button variant="info" size="sm" onClick={this.props.handleShowClientSearchEye}>
                                                <Eye />
                                            </Button>
                                        </InputGroup.Prepend>
                                        <Form.Control
                                            type="text"
                                            value={this.props.ClientName}
                                            onChange={this.props.handleChangeClientName}
                                            placeholder="Search Client Name"
                                            aria-describedby="inputGroupPrepend"
                                            required
                                            />
                                       
                                    </InputGroup>
                                </Form.Group>
                            </Form.Row>
                         <Form.Row>
                         <Form.Group as={Col} md="6" controlId="WeightRegistrationNumber">
                                    <Form.Label>Registration Number</Form.Label>
                                    <Form.Control type="text"  value={this.props.WeightRegistrationNumber}
                                        onChange={this.props.changeWeightRegistrationNumber} readOnly />
                                </Form.Group>
                         </Form.Row>
                            <Form.Row>
                                <Form.Group as={Col} md="12" controlId="InspectionDetails">
                                    <Form.Label>Inspection Details</Form.Label>
                                    <Form.Control
                                        as="textarea" rows="4" 
                                        placeholder="Enter Inspection Details"
                                        value={this.props.InspectionDetails}
                                        onChange={this.props.changeInspectionDetails} 
                                        required
                                        />
                                    <Form.Control.Feedback type="invalid">
                                            Please Enter Inspection Details.
                                        </Form.Control.Feedback>    
                                </Form.Group>
                            </Form.Row>

                            <Form.Row>
                                <Form.Group as={Col} md="6" controlId="SystemStatusType">
                                    <Form.Label>System Status Type</Form.Label>
                                    <InputGroup>

                                        <Form.Control
                                            type="text"
                                            name="SystemStatus"
                                            value={this.props.SystemStatusTypeDescription}
                                            placeholder="Search System Status Type"
                                            aria-describedby="inputGroupPrepend"
                                            required
                                        />
                                        
                                        <InputGroup.Prepend>
                                            <Button variant="info" size="sm" onClick={this.props.handleShowStatusSearch}>
                                                <Search />
                                            </Button>
                                        </InputGroup.Prepend>
                                        <Form.Control.Feedback type="invalid">
                                            Please Select Status.
                                        </Form.Control.Feedback>
                                        
                                    </InputGroup>
                                    
                                </Form.Group>
                            </Form.Row>

                        </Modal.Body>

                        <Modal.Footer>
                            <Button variant="success" onClick={this.props.handleHide}>
                                Cancel
                            </Button>
                            <Button variant="primary" type="submit" disabled={this.props.isEnabled}>
                                {this.props.InsertUpdate}
                            </Button>
                        </Modal.Footer>
                    </Form>
                </Modal>

                <Modal
                    show={this.props.showdelete}
                    onHide={this.props.handleHideDelete}
                >
                    <Modal.Header closeButton>
                        <Modal.Title id="AddUpdate">
                            Delete Weight Inspection
                        </Modal.Title>
                    </Modal.Header>
                    <Form onSubmit={this.props.handleDelete} autoComplete="off">
                        <Modal.Body>
                            <Form.Group controlId="description_del">
                                <h4>Are you sure you want to delete this record?</h4>
                                <Form.Control type="text" placeholder="Click Delete Button" value={this.props.BusinessName} onChange={this.props.changeBusinessName} readOnly />
                            </Form.Group>
                        </Modal.Body>

                        <Modal.Footer>
                            <Button variant="success" onClick={this.props.handleHideDelete}>
                                Cancel
                            </Button>
                            <Button variant="primary" type="submit">
                                Yes Delete
                            </Button>
                        </Modal.Footer>
                    </Form>
                </Modal>
            </div>
        );
    }
}

export default WeightsInspectionModal;