import React from "react"
import { Modal } from 'react-bootstrap';
import { Table, Form } from 'react-bootstrap';


const ClientPopover = props => {
        return (
            <>

                <Modal
                    show={props.opensearch}
                    onHide={props.handleHideSearch}
                    
                >
                    <Modal.Header className="bg-info" closeButton>
                        <Modal.Title >
                         Client Details
                    </Modal.Title>
                    </Modal.Header>                   
                     <Modal.Body>
                     <Table bordered size="sm">
                            <thead>
                                <tr>
                                    <th>#</th>
                                    <th>Details</th>
                                </tr>
                            </thead>
                            <tbody>
                               
                                    <tr>
                                        <td>Client Name</td>
                                        <td>
                                            <Form.Group controlId="ClientName">
                                                <Form.Control type="text"
                                                    name="ClientName"
                                                    value={props.ClientNamePopUp}
                                                    onChange={props.handleChangeClientNamePopUp}
                                                    readOnly />
                                            </Form.Group>
                                        </td>
                                    </tr>
                                    <tr>
                                        <td>Document No</td>
                                        <td>
                                            <Form.Group controlId="DocumentNo">
                                                <Form.Control type="text"
                                                    name="DocumentNo"
                                                    value={props.ClientDocumentNumber}
                                                    onChange={props.changeClientDocumentNumber}
                                                    readOnly />
                                            </Form.Group>
                                        </td>
                                    </tr>
                                    <tr>
                                        <td>Postal Address</td>
                                        <td>
                                            <Form.Group controlId="PostalAddress">
                                                <Form.Control type="text"
                                                    name="PostalAddress"
                                                    value={props.ClientPostalAddress}
                                                    onChange={props.changeClientPostalAddress}
                                                    readOnly />
                                            </Form.Group>
                                        </td>
                                    </tr>
                                   
                                    <tr>
                                        <td>Mobile Number</td>
                                        <td>
                                            <Form.Group controlId="MobileNumber">
                                                <Form.Control type="text"
                                                    name="MobileNumber"
                                                    value={props.ClientMobileNumber}
                                                    onChange={props.changeClientMobileNumber}
                                                    readOnly />
                                            </Form.Group>
                                        </td>
                                    </tr>
                            </tbody>
                        </Table>
                       
                    </Modal.Body>

                </Modal>
            </>

        )
    }



export default ClientPopover